//helpers

@mixin listres {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin inputres {
  margin: 0;
  padding: 0;

  font: inherit;
  color: inherit;

  background-color: transparent;
  border: 0;
  border-radius: 0;

  -webkit-tap-highlight-color: transparent;

  &:focus {
    outline: none;
  }
}

@mixin btnres {
  padding: 0;
  background: none;
  border: 0;
}

@mixin fieldres {
  border: 0;
}

// sass variables for mixins

$tablet-width: 768px;
$desktop-width: 1440px;
$mobile-width: 320px;
$mobile-width-only: 767px;

@mixin mobile-w {
  @media (min-width: $mobile-width) {
    @content;
  }
}

@mixin mobile-w-only {
  @media (max-width: $mobile-width-only) {
    @content;
  }
}

@mixin tablet-w {
  @media (min-width: $tablet-width) {
    @content;
  }
}

@mixin desktop-w {
  @media (min-width: $desktop-width) {
    @content;
  }
}
